import React, {useState} from 'react';
import './App.css';
import {CardViewer} from './features/card/CardViewer';

function App() {
  const imageRange = (first, last) => {
    const x = [];
    for (let i = first; i <= last; i += 2) {
      x.push(i);
    }
    return x;
  };

  const imageNumbers = [
    imageRange(3, 61),
    imageRange(65, 77),
    imageRange(80, 108),
    imageRange(111, 159),
    imageRange(162, 194),
    imageRange(197, 227),
  ].flat();

  const imageFileNames = imageNumbers
      .map(n => [
        `WUZETKI-RĘKA-LETSS-GO (2)-${n.toString().padStart(3, '0')}.jpg`,
        `WUZETKI-RĘKA-LETSS-GO (2)-${(n + 1).toString().padStart(3, '0')}.jpg`,
      ]);

  const [loadImages, setLoadImages] = useState(false);

  const handleLoadImages = () => {
    setLoadImages(true);
  };

  if (loadImages) {
    return (<div>
      <h1>ODSWIEZ SOBIE STRONE TERAZ ZA KILKANASCIE SEKUND JAK SIE ZALADUJA</h1>
      {imageFileNames.flat()
          .map(name => <img
              src={require(`../public/cards/${name}`).default}/>,
          )}
    </div>);
  }

  return (
      <div className="App">
        <CardViewer imageFileNames={imageFileNames}/>
        <button onClick={handleLoadImages}>Wczytaj zdjęcia</button>
      </div>
  );
}

export default App;
