import {useState} from 'react';

import {shuffled} from '../../utils';
import styles from './CardViewer.module.scss';

export function CardViewer({imageFileNames}) {
  const [notLearned, setNotLearned] = useState(shuffled(imageFileNames));
  const [learned, setLearned] = useState([]);
  const [currentCard, setCurrentCard] =
      useState(notLearned[0]);
  const [isAnswer, setIsAnswer] = useState(false);
  const [inputValue, setInputValue] = useState('');

  const [currentQuestCard, currentAnswerCard] = currentCard ?? [];

  const handleShowAnswer = (event) => {
    event.preventDefault();
    setIsAnswer(true);
  };

  console.log(notLearned);

  const handleInputChanged = (event) => {
    setInputValue(event.target.value);
  };

  const goToNext = () => {
    setIsAnswer(false);
    setInputValue('');
    setCurrentCard(notLearned[1]);
  };

  const handleCorrect = (event) => {
    goToNext();
    setLearned([...learned, currentCard]);
    setNotLearned(prev => prev.slice(1));
  };

  const handleIncorrect = (event) => {
    goToNext();
    setNotLearned(prev => [prev[1], prev[2], prev[0], ...prev.slice(3)].filter(it => it !== undefined));
  };

  if (!currentCard) {
    return <div>
      brawo, zdałeś!!!!!!!! <button
        onClick={() => window.history.go(0)}>
      Zacznij od nowa</button> albo idź spać
    </div>;
  }

  return (
      <div>
        <div className={styles.imageGrid}>
          <div className={styles.questImageContainer}>
            <img className={styles.questImage}
                 src={require(
                     `../../../public/cards/${currentQuestCard}`).default}/>
          </div>
          {isAnswer ?
              <div className={styles.answerImageContainer}>
                <img className={styles.answerImage}
                     src={require(
                         `../../../public/cards/${currentAnswerCard}`).default}/>
              </div> :
              ''}
        </div>
        <div>
          {isAnswer ?
              <>
                <button onClick={handleCorrect}>Wiem</button>
                <button onClick={handleIncorrect}>Nie wiem</button>
              </> :
              <>
                <button onClick={handleShowAnswer}>Pokaż odpowiedź</button>
              </>
          }

          <input type="text" value={inputValue} onChange={handleInputChanged} className={styles.input}/>
        </div>
        <div>
          wiesz: {learned.length} | nie wiesz: {notLearned.length}
        </div>
      </div>
  );
}